<template>
  <div class="addtHtml">
  	<div class="addList"><h3><span class="inputLeft">标题:</span><input v-model="title" type="" placeholder="请输入"></h3></div>
  	<div class="addList">
  		<h3>摘要:</h3>
  		<textarea v-model="excerpt" type="" placeholder="请输入"></textarea>
  	</div>
  	
  	<div class="addList">
  		<h3 class="imgH3" data-id="">封面</h3>
  		<!--<input type="file" name="file" id="file" />-->
  		<van-uploader :after-read="onRead">
  			<div class="butIcons">选择封面</div>
		</van-uploader>
  	</div>
  	<div class="addList">
  		<h3>正文:</h3>
  		<textarea v-model="content" type="" style="height: 200px;" placeholder="请输入"></textarea>
  	</div>
  	<br />
  	<div class="butAdd but-80-red" v-on:click="add">提交</div>
  </div>
</template>

<script>
$("title").text("上传图片");
export default {
  name: 'addtHtml',
  data () {
    return {
    		content: '',
    		title:'',
    		excerpt: '',
    }
  },
  created:function(){
    
  },
  methods: {
  	onRead(file) {
      console.log(file)
      if(!file.file.name){return false;}
      var _this = this;
      $("#msgLoding").show();
		var formData = new FormData();
		formData.append("file", $('.van-uploader__input')[0].files[0]);
    		_this.utilAjaxFile('http://121.14.27.27:88/wp-json/wp/v2/media',formData,function(res){
    			$("#msgLoding").hide();
    			$(".butIcons").hide();
    			
    			$(".imgH3").attr("data-id",res.id);
    			$(".van-uploader:after").css("content","")
    			var img = $(".van-uploader img").attr("src");
		    if(img){
		      	$(".van-uploader img").attr("src",file.content);
		    }else{
		      	var imgHtml = '<img class="img" src="'+file.content+'" style="width:24rem;height:16rem; display: block;margin:10px auto;">'
		      	$(".van-uploader").append(imgHtml)
		    }
		},"post");
		
		$(".imgH3").click(function(){
			
		})
    },
    add: function (event) {
    		var _this = this;
    		var user = JSON.parse(localStorage.user);
    		var author = user.id;
    		var token =  user.token;
    		var content = this.content;
    		var title = this.title;
    		var featured_media = $(".imgH3").attr("data-id");
    		if(!title){
    			_this.utilMsg("请填写标题");
    			return false;
    		}else if(!content){
    			_this.utilMsg("请填写内容");
    			return false;
    		}
    		$("#msgLoding").show();
    		var datas = {"content":content,"title":title,"author":author,"excerpt":this.excerpt,"status":'publish',"featured_media":featured_media}
    		$.ajax({
    			type:'post',
    			data: datas,
    			async: true,
			url: 'http://121.14.27.27:88/wp-json/wp/v2/posts',
			beforeSend: function(xhr) {
    				xhr.setRequestHeader('Authorization', 'Bearer '+token);
            },
			success: function(res){
				$("#msgLoding").hide();
				 _this.utilMsg("发布成功")
				 $(".van-uploader img").remove();
				 $(".butIcons").show();
				 $(".imgH3").attr("data-id","");
				 _this.title = '';
				 _this.content = '';
				 _this.excerpt = '';
				 
			},
            error: function(res){
            	$("#msgLoding").hide();
            	 _this.utilMsg("发布失败");
            }
		});
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.addList{margin-bottom: 10px;font-size: 1.5rem;background: #fff;}
	.addList textarea{width:96%;font-size: 1.5rem; height: 100px;padding:2%;border: 0;resize: none;outline:none;}
	.addList h3{background: #fff;padding: 10px 10px;margin: 0;}
	.addList input{border: 0;font-size: 1.5rem;width: 75%;}
	.addList .inputLeft{width: 25%;}
	.van-uploader{margin:0px auto;display: block;position: relative;padding:2rem 0;
		background: url(../assets/xiangji@3x.png) center 4rem no-repeat;background-size:4rem 4rem;
	}
	.butIcons{
		margin: 0 auto;width:11rem;height:3rem;padding-top:7rem;
		text-align:center;color: #999;z-index: 1;border: dashed 1px #c7c7c7;
	}

</style>



